*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --color-basic-text: #111;
  --color-italic-text: #b4afaf;
  --color-brand: #4f2ee8;
  --color-interactive: #eac645;
  --color-white: #fff;
  --color-light-theme: #f6f6f6;
  --color-dark-theme: #202024;
  --cubic-bezier: cubic-bezier(.4, 0, .2, 1);
}

@font-face {
  font-family: DM Sans;
  src: url("DMSans-Normal.331f979e.ttf");
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: DM Sans;
  src: url("DMSans-Italic.ed07c4fe.ttf");
  font-weight: 400;
  font-style: italic;
}

@font-face {
  font-family: DM Sans;
  src: url("DMSans-Medium.b8d1a1bf.ttf");
  font-weight: 500;
  font-style: normal;
}

@font-face {
  font-family: DM Sans;
  src: url("DMSans-MediumItalic.67aaf2c4.ttf");
  font-weight: 500;
  font-style: italic;
}

@font-face {
  font-family: DM Sans;
  src: url("DMSans-Bold.306cbdd3.ttf");
  font-weight: 700;
  font-style: normal;
}

@font-face {
  font-family: DM Sans;
  src: url("DMSans-BoldItalic.15cea227.ttf");
  font-weight: 700;
  font-style: italic;
}

body {
  background-color: var(--color-light-theme);
  color: var(--color-basic-text);
  cursor: url("cursor-light-theme.fb388f10.svg"), default;
  font-family: DM Sans;
}

body.dark-theme {
  background-color: var(--color-dark-theme);
  cursor: url("cursor-dark-theme.a985f96a.svg"), default;
}

main {
  background-color: var(--color-light-theme);
}

.dark-theme main {
  background-color: var(--color-dark-theme);
}

section {
  padding-top: 40px;
  padding-bottom: 40px;
}

p, h1, h2, h3, h4, h5, h6, button, ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

a {
  color: inherit;
  text-decoration: none;
}

svg {
  display: block;
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (min-width: 375px) {
  .container {
    width: 375px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1440px) {
  .container {
    width: 1440px;
    padding-left: 24px;
    padding-right: 24px;
  }
}

.header-container {
  z-index: 2;
  background-color: var(--color-white);
  color: var(--color-basic-text);
  outline: 1.5px solid var(--color-basic-text);
  border-radius: 0 0 8px 8px;
  justify-content: space-between;
  align-items: center;
  padding-top: 18px;
  padding-bottom: 18px;
  display: flex;
  position: relative;
}

.logo-link {
  gap: 8px;
  display: flex;
}

.text {
  font-size: 22px;
  font-weight: 700;
}

.logo-icon {
  width: 109px;
  height: 28px;
}

.header-button-box {
  align-items: center;
  gap: 14px;
  display: flex;
  position: relative;
}

.sign-up-btn, .authorizated-btn, .log-out-btn, .mobile-menu-container.is-hidden {
  display: none;
}

.mobile-menu-open-btn {
  width: 28px;
  height: 28px;
  background-color: var(--color-white);
  border: none;
}

.burger-icon {
  stroke: var(--color-basic-text);
}

.burger-icon:hover, .burger-icon:focus {
  fill: var(--color-brand);
}

.burger-icon .mobile-menu-close-icon {
  display: none;
}

.dark-theme .burger-icon {
  stroke: var(--color-white);
  background-color: var(--color-basic-text);
}

.navigation-main, .sign-up-pc {
  display: none;
}

.mobile-menu-container {
  z-index: 1;
  max-width: 100%;
  background: url("books-mobile.1160b41a.png"), radial-gradient(183.6% 183.01% at 59.76% -23.42%, var(--color-brand) 18.03%, var(--color-white) 100%);
  height: 100%;
  transition: transform 250ms var(--cubic-bezier);
  background-position: bottom;
  background-repeat: no-repeat;
  background-size: contain;
  position: absolute;
  top: 60px;
  transform: translateY(-100%);
}

.mobile-menu-container.is-open {
  width: 100%;
  height: 100%;
  display: block;
  transform: translateY(0);
}

@media screen and (min-width: 490px) {
  .mobile-menu-container {
    background-size: cover;
  }
}

@media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2x) {
  .mobile-menu-container {
    background: url("books-mobile@2x.409d0535.png"), radial-gradient(183.6% 183.01% at 59.76% -23.42%, #4f2ee8 18.03%, var(--color-white) 100%);
    background-position: bottom;
    background-repeat: no-repeat;
    background-size: contain;
  }
}

.sign-up-btn-mobile {
  width: 184px;
  height: 48px;
  letter-spacing: -.04em;
  border: 1.5px solid var(--color-basic-text);
  color: var(--color-white);
  background-color: var(--color-brand);
  border-radius: 18px;
  justify-content: center;
  margin-top: 40px;
  margin-bottom: 130px;
  margin-left: 20px;
  padding-top: 14px;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.29;
  display: flex;
}

.sign-up-icon {
  width: 20px;
  height: 20px;
  fill: #eac645;
  margin-left: 90px;
}

.authorizated-mobile-menu-box {
  flex-direction: column;
  align-items: flex-start;
  display: flex;
}

.authorizated-mobile-menu-user {
  align-items: center;
  display: flex;
}

.authorizated-mobile-menu-user.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.user-icon-box {
  background-color: var(--color-light-theme);
  width: 37px;
  height: 37px;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  margin-right: 9px;
  display: flex;
}

.user-icon {
  width: 19px;
  height: 19px;
  stroke: var(--color-basic-text);
}

.user-name {
  letter-spacing: -.02em;
  color: var(--color-white);
  font-size: 22px;
  font-weight: 700;
  line-height: 1.31;
}

.mobile-navigation-list {
  flex-direction: column;
  align-items: flex-start;
  gap: 16px;
  margin: auto;
  display: flex;
}

.mobile-navigation-list.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.mobile-navigation-item {
  letter-spacing: -.04em;
  text-transform: uppercase;
  color: var(--color-white);
  transition: transform 250ms var(--cubic-bezier);
  font-size: 14px;
  font-weight: 500;
  line-height: 1.29;
}

.mobile-navigation-link {
  align-items: center;
  display: flex;
}

.logo-shop {
  width: 20px;
  height: 20px;
  margin-left: 4px;
}

.log-out-mobile-btn {
  letter-spacing: -.04em;
  width: 118px;
  height: 48px;
  border: 1.5px solid var(--color-basic-text);
  color: var(--color-basic-text);
  background: var(--color-white);
  border-radius: 18px;
  justify-content: center;
  align-items: center;
  margin-top: auto;
  margin-bottom: 20px;
  padding: 14px;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.29;
  display: flex;
}

.log-out-mobile-btn.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}

.log-out-mobile-btn .sign-up-icon {
  margin-left: 4px;
}

.mobile-menu-open-btn.is-open .mobile-menu-close-icon {
  display: block;
}

.mobile-menu-open-btn.is-open .mobile-menu-open-icon {
  display: none;
}

body.mobile-menu-open {
  overflow: hidden;
}

@media screen and (min-width: 768px) {
  .mobile-menu-container, .mobile-menu-container.is-open {
    display: none;
  }

  .header-container {
    z-index: 0;
    justify-content: flex-start;
  }

  .logo-link {
    margin-right: 40px;
  }

  .navigation-main {
    display: block;
  }

  .navigation-list {
    align-items: center;
    gap: 16px;
    display: flex;
  }

  .navigation-item {
    letter-spacing: -.04em;
    text-transform: uppercase;
    color: var(--color-basic-text);
    transition: color 250ms var(--cubic-bezier);
    font-size: 14px;
    font-weight: 500;
    line-height: 1.29;
    position: relative;
  }

  .navigation-link {
    border-radius: 36px;
    align-items: center;
    padding: 8px 16px;
    display: flex;
  }

  .active {
    background-color: var(--color-interactive);
    border-radius: 36px;
    padding: 8px 16px;
    font-weight: 700;
  }

  .navigation-item:hover {
    color: var(--color-brand);
  }

  .current {
    background-color: var(--color-interactive);
    color: var(--color-basic-text);
    border-radius: 36px;
    font-weight: 700;
  }

  .mobile-menu-open-btn {
    display: none;
  }

  .header-button-box {
    align-items: center;
    gap: 24px;
    margin-left: auto;
  }

  .sign-up-btn {
    letter-spacing: -.04em;
    width: 184px;
    height: 48px;
    border: 1.5px solid var(--color-basic-text);
    color: var(--color-white);
    background-color: var(--color-brand);
    transition: transform 250ms var(--cubic-bezier);
    border-radius: 18px;
    justify-content: center;
    align-items: center;
    padding: 14px;
    font-size: 14px;
    font-weight: 500;
    line-height: 1.29;
    display: flex;
  }

  .sign-up-icon {
    margin-left: 90px;
  }

  .authorizated-pc {
    display: none;
  }

  .authorizated-btn {
    background-color: var(--color-brand);
    transition: box-shadow 250ms var(--cubic-bezier);
    border: none;
    border-radius: 36px;
    justify-content: center;
    align-items: center;
    padding: 4px 12px;
    display: flex;
    position: relative;
  }

  .authorizated-btn:hover, .authorizated-btn:focus {
    box-shadow: 0 4px 18px rgba(0, 0, 0, .15);
  }

  .log-out-btn {
    width: 185px;
    height: 48px;
    letter-spacing: -.04em;
    border: 1.5px solid var(--color-basic-text);
    color: var(--color-basic-text);
    background-color: var(--color-white);
    border-radius: 18px;
    justify-content: center;
    align-items: center;
    padding: 14px;
    font-size: 14px;
    font-weight: 500;
    line-height: 1.29;
    display: flex;
    position: absolute;
  }

  .user-icon-arrow {
    width: 23px;
    height: 26px;
    fill: var(--color-white);
    margin-left: 4px;
  }
}

@media screen and (min-width: 1440px) {
  .logo-link {
    margin-right: 76px;
  }

  .navigation-list {
    gap: 24px;
  }

  .sign-up-btn {
    letter-spacing: -.04em;
    width: 184px;
    height: 48px;
    border: 1.5px solid var --color-basic-text;
    color: var(--color-white);
    background-color: var(--color-brand);
    border-radius: 18px;
    justify-content: center;
    align-items: center;
    padding: 14px;
    font-size: 14px;
    font-weight: 500;
    line-height: 1.29;
    display: flex;
  }
}

.dark-theme .header {
  background-color: var(--color-dark-theme);
}

.dark-theme .header-container {
  border: 1.5px solid var(--color-white);
  background-color: var(--color-basic-text);
  border-radius: 0 0 8px 8px;
}

.dark-theme .logo-icon {
  fill: #f3f3f3;
}

.dark-theme .navigation-link {
  color: var(--color-white);
}

.dark-theme .logo-shop, .dark-them .icon-burger-menu, .dark-theme .icon-close {
  fill: var(--color-white);
}

.dark-theme .active {
  color: var(--color-basic-text);
}

.categories-and-support__wrapper {
  display: block;
}

.categories-list {
  margin-bottom: 40px;
}

.book-list {
  display: block;
  position: relative;
}

@media screen and (min-width: 768px) {
  .categories-and-support__wrapper {
    column-gap: 32px;
    margin-bottom: 100px;
    display: flex;
  }

  .categories-list {
    margin-bottom: 0;
  }
}

@media screen and (min-width: 1440px) {
  .home-container {
    display: flex;
  }

  .categories-and-support__wrapper {
    flex-direction: column;
    column-gap: 32px;
  }

  .categories-list {
    margin-bottom: 86px;
  }

  .books-list {
    margin-left: 40px;
  }
}

.categories-list {
  letter-spacing: -.02em;
  color: rgba(17, 17, 17, .6);
  width: 335px;
  height: 228px;
  flex-direction: column;
  row-gap: 24px;
  font-size: 16px;
  line-height: 1.12;
  display: flex;
  position: relative;
  overflow-x: hidden;
  overflow-y: scroll;
}

.current-category {
  color: var(--color-brand);
  text-transform: uppercase;
  font-weight: 700;
}

.categories-list::-webkit-scrollbar {
  width: 8px;
}

.categories-list::-webkit-scrollbar-thumb {
  border-radius: 12px;
}

.dark-theme .categories-list::-webkit-scrollbar-thumb {
  background: rgba(17, 17, 17, .6);
}

.categories-item {
  cursor: pointer;
}

.categories-item:hover {
  color: var(--color-brand);
}

.dark-theme .categories-item:hover {
  color: var(--color-interactive);
}

.dark-theme .categories-list {
  color: rgba(255, 255, 255, .6);
}

.dark-theme .current-category {
  color: var(--color-interactive);
}

@media screen and (min-width: 768px) {
  .categories-list {
    width: 337px;
    height: 472px;
    row-gap: 32px;
    font-size: 18px;
    line-height: 1.33;
  }
}

@media screen and (min-width: 1440px) {
  .categories-list {
    width: 356px;
    height: 472px;
  }
}

.book-card {
  cursor: pointer;
}

.books-list-title {
  padding-bottom: 40px;
  font-size: 32px;
  font-weight: 700;
  line-height: 1.19;
}

.dark-theme .books-list-title {
  color: var(--color-light-theme);
}

.span-books-list-title {
  color: var(--color-brand);
}

.all-categories-title {
  color: var(--color-italic-text);
  text-transform: uppercase;
  letter-spacing: .03em;
  margin-bottom: 18px;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.29;
}

.category-section:nth-child(n+3) {
  padding-top: 40px;
}

.books-container {
  display: block;
}

.book-card-wrapper:not(:first-of-type) {
  padding-top: 40px;
}

.book-card-hover {
  position: relative;
  overflow: hidden;
}

.book-card-hover p {
  width: 100%;
  height: 70px;
  background-color: var(--color-interactive);
  text-align: center;
  text-transform: uppercase;
  visibility: hidden;
  transition: transform 250ms var(--cubic-bezier), visibility 250ms var(--cubic-bezier);
  border-radius: 0 0 8px 8px;
  justify-content: center;
  align-items: center;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.29;
  display: flex;
  position: absolute;
  bottom: 0;
  left: 0;
  transform: translateY(70px);
  box-shadow: 0 0 10px rgba(0, 0, 0, .5);
}

.book-card-hover:hover p, .book-card-hover:focus p {
  visibility: visible;
  transform: translateY(0);
}

.book-image {
  height: 485px;
  width: 335px;
  border-radius: 8px;
  transition: transform .25s ease-out;
  display: block;
  position: relative;
}

.book-info {
  height: 50px;
  padding-top: 14px;
}

.book-title {
  height: 18px;
  text-transform: uppercase;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.16;
  overflow: hidden;
}

.dark-theme .book-title {
  color: var(--color-light-theme);
}

.book-autor {
  height: 14px;
  letter-spacing: -.04em;
  color: var(--color-italic-text);
  margin-top: 4px;
  font-size: 12px;
  font-style: italic;
  line-height: 1.17;
  overflow: hidden;
}

.category-books-button {
  height: 46px;
  width: 124px;
  border: 2px solid var(--color-brand);
  cursor: pointer;
  transition: transform 250ms var(--cubic-bezier);
  border-radius: 40px;
  margin-top: 8px;
  margin-left: auto;
  font-size: 14px;
  font-weight: 700;
  line-height: 1.29;
  display: block;
}

.category-books-button:hover, .category-books-button:focus {
  transform: scale(1.1);
}

.dark-theme .category-books-button {
  color: var(--color-light-theme);
  background-color: inherit;
}

@media screen and (min-width: 768px) {
  .category-section {
    width: 704px;
  }

  .books-list-title {
    font-size: 48px;
    line-height: 1.08;
  }

  .books-container {
    grid-column-gap: 24px;
    grid-row-gap: 40px;
    grid-template-columns: repeat(3, 1fr);
    grid-auto-rows: minmax(335px, auto);
    display: grid;
  }

  .book-card-wrapper:not(:first-of-type) {
    padding-top: 0;
  }

  .book-image {
    height: 316px;
    width: 218px;
  }

  .category-books-button {
    margin-top: 20px;
    display: block;
  }
}

@media screen and (min-width: 1440px) {
  .category-section {
    width: 996px;
  }

  .books-container {
    grid-column-gap: 24px;
    grid-row-gap: 40px;
    grid-template-columns: repeat(5, 1fr);
    grid-auto-rows: minmax(180px, auto);
    display: grid;
  }

  .book-image {
    width: 180px;
    height: 256px;
  }
}

.support {
  width: 335px;
  height: 352px;
  background: radial-gradient(183.6% 183.01% at 59.76% -23.42%, var(--color-brand) 18.03%, var(--color-white) 100%);
  border-radius: 16px;
  margin-bottom: 40px;
  padding: 24px 40px 68px;
  position: relative;
  box-shadow: 0 10px 10px rgba(245, 245, 247, .1);
}

.support-title {
  color: rgba(0, 0, 0, 0);
  letter-spacing: -.04em;
  background: linear-gradient(-225deg, #fbff16 0%, #4854ff 100%) 0 0 / 200%;
  -webkit-background-clip: text;
  background-clip: text;
  font-size: 28px;
  font-weight: 700;
  animation: textclip 3s linear infinite;
  display: flex;
}

@keyframes textclip {
  to {
    background-position: 200%;
  }
}

.trident-icon {
  margin-left: 12px;
}

.foundations-list {
  margin-top: 40px;
}

.foundation {
  opacity: 0;
  transition: opacity 250ms var(--cubic-bezier);
  align-items: center;
  margin-bottom: 20px;
  display: flex;
}

.foundation.hidden {
  display: none;
}

.foundation-counter {
  color: var(--color-white);
  margin-right: 16px;
  font-size: 14px;
}

