//    
.book-card {
  cursor: pointer;
}

.books-list-title {
  font-weight: 700;
  font-size: 32px;
  line-height: 1.19;
  padding-bottom: 40px;
}

.dark-theme .books-list-title {
  color: var(--color-light-theme);
}

.span-books-list-title {
  color: var(--color-brand);
}

.all-categories-title {
  margin-bottom: 18px;
  color: var(--color-italic-text);
  font-weight: 400;
  font-size: 14px;
  line-height: 1.29;
  text-transform: uppercase;
  letter-spacing: 0.03em;
}

.category-section:nth-child(n + 3) {
  padding-top: 40px;
}

.books-container {
  display: block;
}

.book-card-wrapper:not(:first-of-type) {
  padding-top: 40px;
}

.book-card-hover {
  position: relative;
  overflow: hidden;
}

.book-card-hover p {
  position: absolute;
  bottom: 0px;
  left: 0;
  transform: translateY(70px);

  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 70px;

  background-color: var(--color-interactive);
  text-align: center;
  font-weight: 500;
  font-size: 14px;
  line-height: 1.29;
  text-transform: uppercase;
  border-radius: 0px 0px 8px 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
  visibility: hidden;
  transition: transform 250ms var(--cubic-bezier), visibility 250ms var(--cubic-bezier);
}

.book-card-hover:hover p,
.book-card-hover:focus p {
  transform: translateY(0);
  visibility: visible;
}

.book-image {
  position: relative;
  display: block;
  height: 485px;
  width: 335px;
  border-radius: 8px;
  transition: transform 250ms ease-out;
}

.book-info {
  height: 50px;
  padding-top: 14px;
}

.book-title {
  overflow: hidden;
  height: 18px;
  
  font-weight: 700;
  font-size: 16px;
  line-height: 1.16;
  text-transform: uppercase;
}

.dark-theme .book-title {
  color: var(--color-light-theme);
}

.book-autor {
  height: 14px;
  margin-top: 4px;

  overflow: hidden;
  font-size: 12px;
  font-style: italic;
  line-height: 1.17;
  letter-spacing: -0.04em;
  color: var(--color-italic-text);
}

.category-books-button {
  display: block;
  margin-left: auto;
  margin-top: 8px;
  height: 46px;
  width: 124px;
  border-radius: 40px;
  border: 2px solid var(--color-brand);
  font-weight: 700;
  font-size: 14px;
  line-height: 1.29;
  cursor: pointer;
  transition: transform 250ms var(--cubic-bezier);
}

.category-books-button:hover,
.category-books-button:focus {
  transform: scale(1.1);
}

.dark-theme .category-books-button {
  color: var(--color-light-theme);
  background-color: inherit;
}

@media screen and (min-width: 768px) {
  .category-section {
    width: 704px;
  }

  .books-list-title {
    font-size: 48px;
    line-height: 1.08;
  }

  .books-container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-auto-rows: minmax(335px, auto);
    grid-column-gap: 24px;
    grid-row-gap: 40px;
  }

  .book-card-wrapper:not(:first-of-type) {
    padding-top: 0px;
  }

  .book-image {
    height: 316px;
    width: 218px;
  }

  .category-books-button {
    display: block;
    margin-top: 20px;
  }
}

@media screen and (min-width: 1440px) {
  .category-section {
    width: 996px;
  }

  .books-container {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-auto-rows: minmax(180px, auto);
    grid-column-gap: 24px;
    grid-row-gap: 40px;
  }

  .book-image {
    width: 180px;
    height: 256px;
  }
}